document.addEventListener('DOMContentLoaded', function () {
    iniciarApp();
});



function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollnav();
}

function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body')

    window.addEventListener('scroll', function () {
        
        if (sobreFestival.getBoundingClientRect().bottom < 1) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll')
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll')

        }
    });


}


function scrollnav() { //declaramos la funcion

    const enlaces = document.querySelectorAll('.navegacion-principal'); //selecconamos
    //el elemento al que se le aplicaran los efectos

    enlaces.forEach(enlace => { //iteramos por cada elemento para darle una funcionalidad a cada uno
        //ya que como tenemos varios asi lo requiere

        enlace.addEventListener('click', function (e) { //ahora al evento le pasamos el un listener
            // de click

            e.preventDefault(); //Prevenir el comportamiento por default

            const seccionScroll = e.target.attributes.href.value; //seleccionamos el "valor"
            // en este caso el id que se necesita para ir hasta la seccion

            const seccion = document.querySelector(seccionScroll); //ahora le pasamos ese "valor"
            //para seleccionarlo dentro del documento y lo guardamos en una constante;

            seccion.scrollIntoView({ behavior: "smooth" }); //a seccion le adicionamos la 
            //propiedad y un comportamiento
        });
    });
}

function crearGaleria() { //crear la galeria con un cliclo for para que recorrra todas
    //imagenes
    const galeria = document.querySelector('.galeria-imagenes'); //seleccionamos la clase galeria

    for (let i = 1; i <= 12; i++) { //creamos un ciclo que recorra de 1 a 12

        const imagen = document.createElement('picture'); //creamos 12 veces la etiquera picture

        imagen.innerHTML = ` 
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
        `; //Es esta parte agregamos este codigo en cada etiqueta y como se imprime i en casa ocacion
        //cada vez se va incrementando dando lugar a 12 etiquetas diferentes

        imagen.onclick = function () { //le otorgamos el evento onclick a cada una de las imagenes y creamos
            //una funcion
            mostrarImagen(i); // mostrarImagen sera nuestra funcion para pasar el parametro
        }

        galeria.appendChild(imagen); //por ultimo agregamos el codigo al html
    }
}



function mostrarImagen(id) { //llamamos a esa funcion pero esta vez ya le damos el parametro como "id" que sigue siendo
    // i  pero con otro nombre

    // una vez pasado ese parametro podemos Mostrar cada imagen de manera indvidual, salir del modal
    // y salir del modal cuando se le da click en cualquier parte de la pantalla

    const imagen = document.createElement('picture'); //Guardamos en una variable constante y creamos 
    //la etiqueda picture que albergara nuestro demas codigo

    /*Con esta propiedad inyectamos el codigo de abajo dentro
    de la etiqueda creada anteriormente */imagen.innerHTML = ` 
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria">
        `;

    //Creando la imagen mas grande para que se mestre en toda la pantalla
    const modal = document.createElement('DIV'); //creamos un div y la guardamos en una constante

    modal.appendChild(imagen); /* Ahora detro del div le inyectamos todo le codigo de imagen */

    modal.classList.add('modal'); //Se agrega la clase "modal" al div creado anteriormente

    /*Hasta ahora la lo que se creara cuando hagamos click es lo siguiente:
        <div clas="modal">

            <picture>

                <>aqui el source de las imagenes<>

            </picture>
        
        </div>
    */

    //adicionamos una funcion a modal para 
    //quitarlo clikeando en cualquier lugar de la 
    //pantalla
    modal.onclick = function () {
        const body = document.querySelector('body'); //Seleccionamos el cuerpo
        body.classList.remove('fijar-body');//quitamos la clase para que el cuerpo se quede fijo
        modal.remove(); //se remueve el modal
    }

    //Remover con la tecla Escape
    document.addEventListener('keydown', (event) => {

        switch (event.key) {
            case 'Escape':
                const body = document.querySelector('body'); //Seleccionamos el cuerpo
                body.classList.remove('fijar-body');//quitamos la clase para que el cuerpo se quede fijo
                modal.remove();
                break;

            default:
                break;
        }
    });

    //boton para cerrar el modal
    const cerrarModal = document.createElement('P'); //creamos la etiqueta
    cerrarModal.textContent = 'X'; //esa etiqueta llevara una letra X

    //a lo anteriormente creado le adicionamos una funcion onclick
    cerrarModal.onclick = function () { //funcion para cerrar el modal desde la X

        const body = document.querySelector('body'); //seleccionamos el body para poder remover el fijarbody

        body.classList.remove('fijar-body'); //quitamos la clase para fijar el body

        modal.remove();  //se remueve el modal
    }


    cerrarModal.classList.add('btn-cerrar'); //agregamos la clase  btn cerrar a lo anteriormente creado
    //en este caso a nivel del p

    /* <p clas="btn-cerrar">X<p> */

    modal.appendChild(cerrarModal); //le inyectamos a  modal al codigo de cerrar modal
    /*quedando de la siguiente manera

   <div clas="modal">

          <picture>

              <>aqui el source de las imagenes<>

          </picture>

          <p class="btn-cerrar"> X </p>
      
      </div>
    */


    const body = document.querySelector('body');
    body.appendChild(modal); //lo a√±ade al html a nivel del cuerpo, osea dentro del cuerpo del html, 
    //no se crea uno nuevo, este codigo se 
    //adiciona hasta la parte de abajo

    /*<body>

     <div clas="modal">

            <picture>

                <>aqui el source de las imagenes<>

            </picture>

            <p class="btn-cerrar"> X </p>
        
        </div>

    </body> */

    body.classList.add('fijar-body'); //le damos la clase fijar body para que se quede fijo
}